<template>
  <div class="copc-api-code mt-4" v-if="isCopc">
    <b-card no-body>
      <b-card-header header-tag="header" class="p-2">
        <b-button block v-b-toggle.copc-api-collapse variant="outline-secondary" size="sm" squared>
          <span class="chevron" aria-hidden="true">
            <b-icon-chevron-down v-if="expanded" />
            <b-icon-chevron-right v-else />
          </span>
          <b-icon-code-slash class="mr-1" /> API Access Code
        </b-button>
      </b-card-header>
      <b-collapse id="copc-api-collapse" v-model="expanded">
        <b-card-body>
          <b-tabs content-class="mt-3" small>
            <b-tab title="Python" active>
              <pre class="api-code-block"><code>{{ pythonCode }}</code></pre>
              <b-button size="sm" variant="outline-primary" @click="copyCode(pythonCode)">
                <b-icon-clipboard /> {{ copyLabel }}
              </b-button>
            </b-tab>
            <b-tab title="JavaScript">
              <pre class="api-code-block"><code>{{ jsCode }}</code></pre>
              <b-button size="sm" variant="outline-primary" @click="copyCode(jsCode)">
                <b-icon-clipboard /> {{ copyLabel }}
              </b-button>
            </b-tab>
            <b-tab title="PDAL">
              <pre class="api-code-block"><code>{{ pdalCode }}</code></pre>
              <b-button size="sm" variant="outline-primary" @click="copyCode(pdalCode)">
                <b-icon-clipboard /> {{ copyLabel }}
              </b-button>
            </b-tab>
          </b-tabs>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import { BCollapse, BTabs, BTab, BIconChevronRight, BIconChevronDown, BIconCodeSlash, BIconClipboard } from 'bootstrap-vue';
import { mapState } from 'vuex';

export default {
  name: 'CopcApiCode',
  components: {
    BCollapse,
    BTabs,
    BTab,
    BIconChevronRight,
    BIconChevronDown,
    BIconCodeSlash,
    BIconClipboard
  },
  props: {
    asset: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      expanded: false,
      copyLabel: 'Copy'
    };
  },
  computed: {
    ...mapState(['data']),
    isCopc() {
      return this.asset && this.asset.type === 'application/vnd.laszip+copc';
    },
    copcUrl() {
      return this.asset?.href || '';
    },
    bbox() {
      return this.data?.bbox || null;
    },
    projBbox() {
      return this.data?.properties?.['proj:bbox'] || null;
    },
    projEpsg() {
      return this.data?.properties?.['proj:epsg'] || null;
    },
    bboxStr() {
      if (!this.bbox) return 'null';
      return `[${this.bbox.join(', ')}]`;
    },
    projBboxStr() {
      if (!this.projBbox) return 'null';
      return `[${this.projBbox.join(', ')}]`;
    },
    pythonCode() {
      const url = this.copcUrl;

      if (this.projEpsg && this.projBbox) {
        return `# Python - Read COPC Point Cloud with PDAL
# Install: pip install pdal
# Full dataset bbox in WGS84: ${this.bboxStr}
# Native coordinate system: EPSG:${this.projEpsg}
# Native bbox: ${this.projBboxStr}

import pdal
import json

url = "${url}"

# Read full dataset (this may take time for large files)
pipeline_json = json.dumps({
    "pipeline": [
        {"type": "readers.copc", "filename": url}
    ]
})

pipeline = pdal.Pipeline(pipeline_json)
count = pipeline.execute()
print(f"Total points: {count:,}")

# Access point data as numpy array
arrays = pipeline.arrays
if arrays:
    arr = arrays[0]
    print(f"Fields: {arr.dtype.names}")
    print(f"X range: {arr['X'].min():.2f} - {arr['X'].max():.2f}")
    print(f"Y range: {arr['Y'].min():.2f} - {arr['Y'].max():.2f}")
    print(f"Z range: {arr['Z'].min():.2f} - {arr['Z'].max():.2f}")`;
      }

      return `# Python - Read COPC Point Cloud with PDAL
# Install: pip install pdal

import pdal
import json

url = "${url}"

pipeline_json = json.dumps({
    "pipeline": [
        {"type": "readers.copc", "filename": url}
    ]
})

pipeline = pdal.Pipeline(pipeline_json)
count = pipeline.execute()
print(f"Total points: {count:,}")`;
    },
    jsCode() {
      const url = this.copcUrl;

      return `// JavaScript - Query COPC Point Cloud
// Install: npm install copc
import { Copc } from 'copc';

const url = "${url}";

async function loadPoints() {
  const copc = await Copc.create(url);
  const { header, info } = copc;

  console.log('Point count:', header.pointCount);
  console.log('Bounds:', info.cube);

  // Read hierarchy and nodes
  const hierarchy = await Copc.loadHierarchyPage(url, info.rootHierarchyPage);
  console.log('Hierarchy nodes:', Object.keys(hierarchy.nodes).length);
}

loadPoints();`;
    },
    pdalCode() {
      const url = this.copcUrl;

      // Don't include bounds by default - coordinate systems may not match
      let code = `{
  "pipeline": [
    {
      "type": "readers.copc",
      "filename": "${url}"
    },
    {
      "type": "writers.las",
      "filename": "output.las"
    }
  ]
}

# Run with: pdal pipeline pipeline.json`;

      // Add helpful comments about coordinate systems
      if (this.projEpsg && this.projBbox) {
        code += `

# Note: This file uses EPSG:${this.projEpsg} coordinate system
# Native bounds (for filtering): ${this.projBboxStr}
# To filter by bounds, add to readers.copc:
#   "bounds": "([minX, maxX], [minY, maxY])"
# Use native coordinates (EPSG:${this.projEpsg}), NOT WGS84 lat/lng`;
      }

      return code;
    }
  },
  methods: {
    async copyCode(code) {
      try {
        await navigator.clipboard.writeText(code);
        this.copyLabel = 'Copied!';
        setTimeout(() => {
          this.copyLabel = 'Copy';
        }, 2000);
      } catch (e) {
        console.error('Failed to copy:', e);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.copc-api-code {
  .api-code-block {
    background: #1e1e1e;
    color: #d4d4d4;
    padding: 12px;
    border-radius: 4px;
    font-size: 12px;
    overflow-x: auto;
    max-height: 300px;
    margin-bottom: 10px;
  }

  .chevron {
    margin-right: 8px;
  }
}
</style>
